import { HttpClient } from '@angular/common/http';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { AppConstants } from 'src/app/shared/config/app.constant';
import { permissionsLabels } from 'src/app/shared/models/fmosNewRolePermissions';
import { AddSystemService } from 'src/app/shared/services/add-system.service';
import { RoleService } from 'src/app/shared/services/role.service';
import { RuleService } from 'src/app/shared/services/rule.service';
import { ToastService } from 'src/app/shared/services/toast.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'npr-rule-list',
  templateUrl: './rule-list.component.html',
  styleUrls: ['./rule-list.component.scss']
})
export class RuleListComponent implements OnInit {
  rulesData: any = [];
  dtTrigger: any = new Subject();
  dtOptions: DataTables.Settings = {};
  isLoading:boolean = false;
  roleCodes = new permissionsLabels();
  currentUser: string;
  ruleDto = new RuleDto();
  ruleConfig = new RuleConfig();
  validationSystems: any;
  sysRuleDetails: any;
  ruleForm: any;
  closeBtn: boolean;
  authBtn: boolean;
  editBtn: boolean;
  deleted: boolean;
  arrRuleValidation: any;
  multipleValidation: MultipleValidation[];
  authBtnHide: boolean;
  reopenBtn: boolean;
  authorizeDone: boolean;
  statusArr: String[] = ['Authorized', 'Unauthorized', 'Open', 'Close'];
  totalPages: any;
  totalRecords: any;
  API_URL = AppConstants.baseURL;
  constructor(private ruleService: RuleService,private roleService:RoleService,
              private toastService: ToastService,
              private router:Router,
              private addSystem:AddSystemService,
              private cdr:ChangeDetectorRef,
              private http: HttpClient) { }

  ngOnInit(): void {
    this.dtOptions = {
      pagingType: 'full_numbers',
      pageLength : 5,
      serverSide: true,
      columnDefs: [ { type: 'date', 'targets': [4] } ],
      order: [[4, 'desc']],
      // processing: true,
      lengthMenu: [
        [5, 10, 20, 30,-1],
        [5, 10, 20, 30,"ALL"],
      ],
      // columnDefs: [ { type: 'date', 'targets': [5] } ],
      // order: [[5, 'desc']],
      ajax: (dataTablesParameters: any, callback) => {
        
        dataTablesParameters.minNumber = dataTablesParameters.start + 1;
        dataTablesParameters.maxNumber =
          dataTablesParameters.start + dataTablesParameters.length;
        console.log(
          dataTablesParameters.minNumber,
          dataTablesParameters.maxNumber
        );
        const pageNo = Math.floor(dataTablesParameters.minNumber / dataTablesParameters.length);
        const pageSize = dataTablesParameters.length;
        const search=dataTablesParameters.search.value
        this.http.get<any>(`${this.API_URL}`+`/ruleConfig/getRuleConfigSummary?pageNo=${pageNo}&pageSize=${pageSize}&search=${search}`)
          .subscribe(resp => {
            ;
            console.log(resp);
            for (let item of resp.result) {
              item.recordStatus = this.statusArr.find((i) =>
                i.startsWith(item.recordStatus)
              );
              item.approvedStatus = this.statusArr.find((i)=>
              i.startsWith(item.approvedStatus)
              );
            }
            
            this.totalPages = resp.totalNoOfPages;
            this.totalRecords = resp.totalRecords;
            this.rulesData = resp.result;
            this.isLoading = false;
            this.cdr.markForCheck();

            callback({
              recordsTotal: resp.totalRecords,
              recordsFiltered: resp.totalRecords,
              data: [],
            });
          });
      },
    };
    this.currentUser = localStorage.getItem('userFromLogin');

    // this.fetchRules();
    this.roleService.fetchScreenPermissions('Create Rule');
    this.roleService.screenLabelList.subscribe(message => this.roleCodes = message);
    console.log(this.roleCodes);
  }

  fetchRules() {
    this.isLoading = true;
    this.ruleService.fetchingRules()
      .subscribe((resp: any) => {
        console.log(resp);
        // this.toastService.successMessage('Data Fetched Successfully!.', '');
        for (let data of resp.result) {
          data.approvedStatus = this.statusArr.find((i) =>
            i.startsWith(data.approvedStatus)
          );
  
          data.recordStatus = this.statusArr.find((i) =>
            i.startsWith(data.recordStatus)
          );
        }

        this.rulesData = resp.result;
        this.isLoading = false;
        this.cdr.markForCheck();
        
        this.dtTrigger.next();
      }, (err: any) => {
        console.error("Error!");
        // this.toastService.errorMessage('Failed to Fetch Data!.', '');
      })
  }

  editRule(row) {
    console.log(row);
    this.router.navigate(["/createrule/edit", row.ruleId])
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }
  close(data){
    console.log(data);
    this.ruleConfig = data;

    this.arrRuleValidation = [data];
    this.multipleValidation = [];
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to close record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ", result);
      console.log(this.multipleValidation);
      console.log(this.arrRuleValidation);
      console.log(this.ruleConfig);
      console.log(this.currentUser);
      
      if (result.isConfirmed === true) {
        this.ruleService.editRuleConfig("close", data, this.currentUser).subscribe(closeResp => {
          console.log(closeResp);
          
          if (closeResp) {
            this.ruleConfig = closeResp;
            this.editBtn = true;
            this.authBtn = true;
            this.deleted = false;
            this.reopenBtn = false;
            Swal.fire({ text: "Record is Closed" }).then(() => window.location.reload());
          }
        }, err => {
          Swal.fire({ text: "Failed to close the Record" });
        })
      }
    }))

  }
  open(data){
    this.ruleConfig = data;

    this.arrRuleValidation = [data];
    this.multipleValidation = [];
    
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to re-open record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ", result);
      console.log(this.multipleValidation);
      console.log(this.arrRuleValidation);
      console.log(this.ruleConfig);
      console.log(this.currentUser);
      
      if (result.isConfirmed === true) {
       
       

        this.ruleService.editRuleConfig("open", data, this.currentUser).subscribe(openResp => {
          console.log(openResp);
          
          if (openResp) {
            this.ruleConfig = openResp;
            this.authBtnHide = false;
            this.editBtn = false;
            this.authBtn = false;
            Swal.fire({ text: "Record is Reopened" }).then(() => window.location.reload());
          }
        }, err => {
          Swal.fire({ text: "Failed to open the Record" });
        })
      }
    }))

  }
  delete(data){
    console.log(data);
    
     this.ruleConfig = data;

     this.arrRuleValidation = [data];
     this.multipleValidation = [];
     
    
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to delete record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3e54b8',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info', iconColor: "#d33"
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        this.ruleConfig.mdmtValidationSystemData = this.multipleValidation;
        this.ruleConfig.mdmtValidationSystemData.forEach(el => {
          
          
          el.ruleId = this.ruleConfig.ruleId;
        });
       
        
        this.arrRuleValidation.forEach(el => { el.ruleId = this.ruleConfig.ruleId });
        console.log(this.arrRuleValidation);
        this.ruleConfig.sysRuleDetails = this.arrRuleValidation;
        console.log(this.ruleConfig);
        this.ruleService.onDeleteRuleCongif(this.ruleConfig.sysRuleId, this.currentUser).subscribe(
          (response) => {
            if (response) {
              this.deleted = true;
              this.editBtn = true;
              this.authBtn = true;
              this.closeBtn = true;

              Swal.fire({
                text: 'Record is Deleted',
                icon: 'success'
              }).then(() => window.location.reload());

            }
            else {
              Swal.fire("Failed to delete the reocrd");
            }
          });
      }
    }))

  }

  auth(data){
    this.ruleConfig = data;
    console.log(data);
    
    console.log(this.ruleConfig.creatorId);


     this.arrRuleValidation = [data];
     this.multipleValidation = [];
    console.log(this.currentUser)
    Swal.fire(
      {
        //text: 'Unable to process' + 'Error ' + this.responseforfileupload.errorMessage + 'Do you want to Proceed??',
        text: 'You are trying to Authorize record. ' + ' Do you want to proceed?',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        // confirmButtonText: 'PROCEED.'
        cancelButtonText: 'NO',
        confirmButtonText: 'YES', 'icon': 'info'
      }
    ).then((result => {
      console.log("this is reopen ", result);
      if (result.isConfirmed === true) {
        if (this.ruleConfig.creatorId == this.currentUser) {
          Swal.fire({ text: "Maker cannot authorize the record!" });
        }
        else {
          // this.ruleConfig.mdmtValidationSystemData = this.multipleValidation;
          // this.ruleConfig.mdmtValidationSystemData.forEach(el => {
          //   el.ruleId = this.ruleConfig.ruleId;
          // })
          // // console.log(this.ruleConfig);
          // this.arrRuleValidation.forEach(el => { el.ruleId = this.ruleConfig.ruleId });
          // console.log(this.ruleConfig);
          // this.ruleConfig.sysRuleDetails = this.arrRuleValidation;
          // this.ruleConfig.approverId= this.currentUser;
          // this.ruleConfig.approvedEver == 'NO'? this.ruleConfig.approvedEver = 'N' : (this.ruleConfig.approvedEver == 'YES'? this.ruleConfig.approvedEver = 'Y' : this.ruleConfig.approvedEver);
          // this.ruleConfig.approvedStatus == 'UNAUTHORIZED'? this.ruleConfig.approvedStatus = 'U' : (this.ruleConfig.approvedStatus == 'AUTHORIZED'? this.ruleConfig.approvedStatus = 'A' : this.ruleConfig.approvedStatus);
          // this.ruleConfig.recordStatus == "OPEN" ? this.ruleConfig.recordStatus = 'O' : (this.ruleConfig.recordStatus == 'CLOSE'? this.ruleConfig.recordStatus = 'C' : this.ruleConfig.recordStatus);
         
      if (this.ruleConfig.approvedStatus === "Unauthorized") {
        this.ruleConfig.approvedStatus = 'U'
      }
      if (this.ruleConfig.approvedStatus === 'Authorized') {
        this.ruleConfig.approvedStatus = 'A'
      }
      if (this.ruleConfig.recordStatus === 'Open') {
        this.ruleConfig.recordStatus = 'O'
      }
      if (this.ruleConfig.recordStatus === 'Close') {
        this.ruleConfig.recordStatus = 'C'
      }
      if (this.ruleConfig.approvedEver === 'NO') {
        this.ruleConfig.approvedEver = 'N'
      }
      if (this.ruleConfig.approvedEver === 'YES') {
        this.ruleConfig.approvedEver = 'Y'
      }
          console.log(this.ruleConfig);
          this.ruleService.editRuleConfig("auth", data, this.currentUser).subscribe(authResp => {
            console.log(authResp);
            
            if (authResp) {
              this.ruleConfig = authResp;
              this.authorizeDone = true;

              Swal.fire({ text: "Record is Authorized" }).then(() => window.location.reload());
            }

          }, (err) => {
            Swal.fire({ text: "Failed To Authorize" });
          })
        }
      }
    }))

  }
    
  
}

export class RuleConfig {
  id: any;
  sysRuleId: any;
  ruleId: any;
  ruleName: any;
  sourceTranslationId: any;
  targetTranslationId: any;
  validationSystems: any;
  creatorId: any;
  createdTime: any;
  approverId: any;
  approvedTime: any;
  versionNo: any;
  approvedStatus: string;
  approvedEver: string;
  recordStatus: string;
  mdmtValidationSystemData: Array<MultipleValidation>;
  sysRuleDetails: Array<RuleValidation>;
}
export class RuleValidation {
  id: any;
  ruleId: any;
  validationMethod: any;
  validationDetail: any;
}
export class RuleDto {
  msgRule: RuleConfig;
  msgValidation: Array<RuleValidation>;
}
export class MultipleValidation {
  validationSystem: any;
  messageType: any;
  service: any;
  operation: any;
  id: any;
  ruleId: any;
}
