import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { RuleDetailsComponent } from './rule-details/rule-details.component';
import { RuleDetails2Component } from './rule-details2/rule-details2.component';
import { RuleListComponent } from './rule-list/rule-list.component';

const routes: Routes = [
  {
    path: 'list',
    component: RuleListComponent
  },{
    path: 'create',
    component: RuleDetailsComponent,
    canDeactivate: [CanDeactivateGuard]
  },{
    path: 'edit/:id',
    component: RuleDetails2Component,
    canDeactivate: [CanDeactivateGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RuleRoutingModule { }
