  onValidtion(event, i) {
    this.validationData = event;
    this.selectedExternalSystems[i] = event;
    const selectedMessageKeySet = new Set(this.selectedExternalSystems);
    this.filteredExternalSystems = this.externalsystem.filter(system => !selectedMessageKeySet.has(system));
    this.rules.gettingTranslationData(this.validationData).subscribe((resp:any) => {
      console.log(resp);
      this.validationTransData = resp;
      this.xmlRespData=this.validationTransData.msgTransDetails;
      if (resp) {
        
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('service').patchValue(this.validationTransData.serviceName);
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('operation').patchValue(this.validationTransData.operationName);
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('messageType').patchValue(this.validationTransData.messageType);
        this.gettingoperationSndservForValidation(this.validationData, resp.messageType);
      }
    })
  }

  onGetValidtion(data, i) {
    let validationData = data;
    this.rules.gettingTranslationData(validationData).subscribe((resp:any) => {
      console.log(resp);
      if (resp) {
        this.validationTransData = resp;
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('service').patchValue(this.validationTransData.serviceName);
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('operation').patchValue(this.validationTransData.operationName);
        (this.ruleForm.get('validationSystems') as FormArray).at(i).get('messageType').patchValue(this.validationTransData.messageType);
        this.gettingoperationSndservForValidation(validationData, resp.messageType);
      }
    })
  }
 
